<template>
    <div class="reservation-date-container">
        <TopNav activeRouterName="检查预约" goBack />
        <div class="main-title">北京电力医院</div>
        <PersonInfo name="张三" showName />
        <div class="main-container">
            <CardInfo title="科室：放射科" />
            <van-calendar
                title="日历"
                :poppable="false"
                :show-confirm="false"
                :min-date="minDate"
                :max-date="maxDate"
                first-day-of-week="6"
            />
            <div class="date-box">
                上午：
                <van-row gutter="20">
                    <van-col
                        span="12"
                        v-for="(item, index) in amDateList"
                        :key="index"
                    >
                        <van-row
                            type="flex"
                            align="center"
                            justify="space-between"
                            class="choose-box"
                        >
                            <span>{{ item.date }}</span>
                            <van-button
                                :disabled="item.disabled"
                                size="mini"
                                type="info"
                                @click="chooseEvent()"
                                >选择</van-button
                            >
                        </van-row>
                    </van-col>
                </van-row>
                下午：
                <van-row gutter="20">
                    <van-col
                        span="12"
                        v-for="(item, index) in pmDateList"
                        :key="index"
                    >
                        <van-row
                            type="flex"
                            align="center"
                            justify="space-between"
                            class="choose-box"
                        >
                            <span>{{ item.date }}</span>
                            <van-button
                                :disabled="item.disabled"
                                size="mini"
                                type="info"
                                @click="chooseEvent()"
                                >选择</van-button
                            >
                        </van-row>
                    </van-col>
                </van-row>
            </div>
        </div>
    </div>
</template>

<script>
import TopNav from "@/components/TopNav";
import PersonInfo from "@/components/PersonInfo";
import CardInfo from "@/components/CardInfo";
export default {
    name: "Home",
    components: {
        TopNav,
        PersonInfo,
        CardInfo,
    },
    data() {
        return {
            minDate: "",
            maxDate: "",
            amDateList: [
                {
                    date: "8:30-9:00",
                    disabled: false,
                },
                {
                    date: "9:00-9:30",
                    disabled: false,
                },
                {
                    date: "8:30-10:00",
                    disabled: false,
                },
                {
                    date: "10:00-10:30",
                    disabled: true,
                },
                {
                    date: "10:30-11:00",
                    disabled: false,
                },
                {
                    date: "11:00-11:30",
                    disabled: false,
                },
            ],
            pmDateList: [
                {
                    date: "13:30-14:00",
                    disabled: false,
                },
                {
                    date: "14:30-15:00",
                    disabled: false,
                },
                {
                    date: "15:00-15:30",
                    disabled: true,
                },
                {
                    date: "15:30-16:00",
                    disabled: false,
                },
                {
                    date: "16:00-16:30",
                    disabled: false,
                },
                {
                    date: "16:30-15:00",
                    disabled: false,
                },
            ],
        };
    },
    created() {
        this.setDate();
    },
    methods: {
        setDate() {
            // 第一步: 获取今天是本周的第几天
            const weekOfday = this.$moment().format("E");
            // 第二步: 获取本周周一的日期
            const lastMonday = this.$moment().subtract(weekOfday - 1, "days");
            // .format();
            // .format("YYYY-MM-DD");
            // 第三步: 获取本周周末的日期
            const lastSunday = this.$moment().add(7 - weekOfday, "days");
            // .format();
            // .format("YYYY-MM-DD");
            (this.minDate = lastMonday._d), (this.maxDate = lastSunday._d);
        },
        chooseEvent() {
            this.$router.go(-1);
        },
    },
};
</script>
<style lang="scss">
.reservation-date-container {
    .van-calendar__day--disabled {
        display: none;
    }
    .van-calendar__month-mark {
        display: none;
    }
}
</style>
<style scoped lang="scss">
.main-container {
    padding: 10px;
    .card-info-container {
        margin-bottom: 10px;
    }
}
.choose-box {
    background: #eee;
    margin-bottom: 10px;
}
</style>
