<template>
    <div class="about">
        <TopNav
            activeRouterName="检查预约"
            originRouterName="AppointmentResult"
        />
        <div class="main-title">北京电力医院</div>
        <PersonInfo name="张三" sex="男" />
        <div class="main-container">
            <CardInfo title="预约建议" />
            <ReservationList
                :labelObj="labelObj"
                v-for="(item, index) in arrList"
                :index="index"
                :key="index"
                :item="item"
                @editEvent="editEvent"
                showButton
            />
            <van-row type="flex" justify="center">
                <van-col span="20"
                    ><van-button
                        class="main-bg-color"
                        type="info"
                        block
                        size="small"
                        @click="goToRouter()"
                        >确认预约</van-button
                    ></van-col
                >
            </van-row>
        </div>
    </div>
</template>

<script>
import TopNav from "@/components/TopNav";
import PersonInfo from "@/components/PersonInfo";
import CardInfo from "@/components/CardInfo";
import ReservationList from "@/components/ReservationList";
export default {
    name: "Home",
    components: {
        TopNav,
        PersonInfo,
        CardInfo,
        ReservationList,
    },
    data() {
        return {
            labelObj: {
                labelOne: "检查科室",
                labelTwo: "检查项目",
                labelThree: "预约检查时间",
            },
            arrList: [
                {
                    listNumber: "1252121",
                    projectName: "鼻窦ct检查",
                    date: "2022-02-02 11：00",
                },
                {
                    listNumber: "1252121",
                    projectName: "鼻窦ct检查",
                    date: "2022-02-02 11：00",
                },
                {
                    listNumber: "1252121",
                    projectName: "鼻窦ct检查",
                    date: "2022-02-02 11：00",
                },
            ],
        };
    },
    methods: {
        // 确认预约按钮
        goToRouter() {
            this.$dialog
                .confirm({
                    title: "预约成功",
                    // theme: 'round-button',
                    confirmButtonText: "查看预约结果",
                    cancelButtonText: "查看我的预约",
                    // message: "弹窗内容",
                })
                .then(() => {
                    this.$router.push({
                        name: "AppointmentResult",
                    });
                })
                .catch(() => {
                    this.$router.push({
                        name: "MyAppointment",
                    });
                });
        },
        // 修改按钮
        editEvent() {
            this.$router.push({
                name: "ReservationDate",
            });
        },
    },
};
</script>
<style scoped lang="scss">
.main-container {
    padding: 10px;
    .card-info-container {
        margin-bottom: 10px;
    }
}
</style>
